/* --- responsiveMainNav --------------------- */
#responsiveMainNavToggler {
  padding: 0;
  height: 33px;
  width: 33px;
  text-align: center;
  line-height: 33px;
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 18px;
  overflow: hidden;
  margin-right: 15px;
  float: right;
  display: none;
}
#responsiveMainNavToggler.opened {
