input[type=range] {
    box-sizing: border-box;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    width: 100%;
    margin: 0;
    padding: 0 2px;
    /* Add some L/R padding to ensure box shadow of handle is shown */

    overflow: hidden;
    border: 0;
    border-radius: 10px;
    outline: none;
    background: -webkit-gradient(linear, left top, left bottom, from(#3897cf), to(#d4d4d4)) no-repeat center;
    background: linear-gradient(#3897cf, #d4d4d4) no-repeat center;
    /* Use a linear gradient to generate only the 2px height background */

    background-size: 100% 3px;
    pointer-events: none;
}
input[type=range]:active,
input[type=range]:focus {
    outline: none;
}
input[type=range]::-webkit-slider-thumb {
    height: 10px;
    width: 10px;
    border-radius: 28px;
    background-color: #ffffff;
    border: 2px solid#0b7bc1;
    position: relative;
    margin: 5px 0;
    /* Add some margin to ensure box shadow is shown */

    cursor: pointer;
    -webkit-appearance: none;
            appearance: none;
    pointer-events: all;
    box-shadow: 0 1px 4px 0.5px rgba(0, 0, 0, 0);
}
input[type=range]::-webkit-slider-thumb::before {
    content: ' ';
    display: block;
    position: absolute;
    top: 13px;
    left: 100%;
    width: 2000px;
    height: 2px;
    background-color:#0b7bc1;
}
.multi-range {
    position: relative;
    min-height: 10px;
}
.multi-range input[type=range] {
    position: absolute;

}
.multi-range input[type=range]:nth-child(1)::-webkit-slider-thumb::before {
    background-color: red;
}
.multi-range input[type=range]:nth-child(2) {
    background: none;
}
.multi-range input[type=range]:nth-child(2)::-webkit-slider-thumb::before {
    background-color: #000000;
}


